import React from 'react';
import Wrapper from '../wrapper/wrapper.component';
import PhotoList from './PhotoList.jsx';
import { photo } from './photos';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";
import './photos.css'

 
const handleDragStart = (e) => e.preventDefault();

  const items = [
    <img src="https://raw.githubusercontent.com/cristenrh/grease/master/public/images/marquee.png" onDragStart={handleDragStart} />,
    <img src="https://raw.githubusercontent.com/cristenrh/grease/master/public/images/slate.png" onDragStart={handleDragStart} />,
    <img src="https://raw.githubusercontent.com/cristenrh/grease/master/public/images/theatre.png" onDragStart={handleDragStart} />,
  ];



  const Photos = () => {
      
    return (
        <Wrapper>
            <div id="photos"></div>
               <div>
                <h1>Photo Gallery</h1>
                <AliceCarousel mouseTracking items={items} />  
            </div>    
          </Wrapper>
            
      
    )
  }


